<template>
  <div class="panel">
    <h1>{{getClient.nombre}} {{getClient.apellidos}}</h1>
    <p>Cedula: {{getClient.cedula}}</p>
    <p>Celular: {{getClient.celular}}</p>
    <p>Direccion: {{getClient.direccion}}</p>
    <p>Dia de Cobro: {{getClient.diacobro}}</p>

    <button
      class='btn btn-secondary'
      @click="setEdit(getClient)"
      :disabled="getIsLoading"
      >
      Editar
    </button>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  computed:{
    ...mapGetters('cliente',['getClient','getIsLoading'])
  },
  methods:{
    ...mapMutations('cliente',['setEdit']),
  }
}
</script>

<style scoped>
  .panel{
    background-color: rgb(2, 2, 26);
    color: white;
    margin-right: 20px;
    padding: 50px;
    text-align: justify;

  }
</style>

